(function ($) {

    $(document).ready(function(){
        var windowSize = $(window).width();
        // Open main-menu
        $('.button__menu-open').on(
            'click', function (e) {
                var mainMenu = $('.menu');
                if(mainMenu.hasClass('active')){
                    $(this).removeClass('open');
                    mainMenu.removeClass('active')
                }else{
                    $(this).addClass('open');
                    mainMenu.addClass('active')
                }
            }
        );
        // Close main-menu
        $('.button__menu-close').on(
            'click', function (e) {
                $('.button__menu-container__open').removeClass('open');
                $('html').removeClass('menu-mobile--active');
            }
        );
        if(windowSize <= 768){
            menuForMobile();
        }
    })

    $(window).resize(
        function () {
            $('.button__menu-container__open').removeClass('open');
            $(".header__menu .read__article").hide();
            $('html').removeClass('menu-mobile--active');
            menuForMobile();
        }
    );

    function menuForMobile(){
        $('.menu-item-has-children a').click(function(){
            var parentMenuItem = $(this).parent();
            if(parentMenuItem.find('.sub-menu').length){
                var subMenu = parentMenuItem.find('.sub-menu');
                if(!subMenu.hasClass('active')){
                    console.log('eeee');
                    $('#header .menu .sub-menu').removeClass('active')
                    subMenu.addClass('active');
                }
            }
        });
    }
})(jQuery);